* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  color: white;
}

body {
  background: #202020;
}

.contenedor {
  width: 80%;
  margin: auto;
  overflow: hidden;
  margin-top: 10vh;

  border-radius: 20px;
  background: #202020;
  box-shadow: 20px 20px 60px #1b1b1b, -20px -20px 60px #252525;
}

.cover-page {
  /* background-image: url("./Fondo.jpg"); */
  background-size: cover;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
}

.cover-page h1 {
  color: rgb(244, 223, 253);
  font-weight: 700;
  letter-spacing: 5px;

  text-shadow: 0 0 7px #fff, 0 0 10px rgb(179, 0, 255),
    0 0 21px rgb(179, 0, 255), 0 0 42px rgb(244, 223, 253);
}

.cuerpo {
  padding: 20px;
  background: #202020;
}

hr {
  border: 1px solid #303030;
  margin: 0 20px;
}

/* From uiverse.io by @Jaareet */
.input {
  font-weight: 500;
  font-size: 0.8vw;
  color: #fff;
  background-color: #1b1b1b;
  box-shadow: 0 0 0.4vw rgba(0, 0, 0, 0.5), 0 0 0 0.15vw transparent;
  border-radius: 0.4vw;
  border: none;
  outline: none;
  padding: 0.4vw;
  max-width: 100%;
  transition: 0.4s;
  padding: 0 20px;
}

.input:hover {
  box-shadow: 0 0 0 0.15vw #bf21f338;
}

.input:focus {
  box-shadow: 0 0 0 0.15vw #bf21f3;
}

.cuerpo-primer-contenedor {
  width: 100%;

  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 25px;
  grid-template-areas: ". .";

  margin-bottom: 20px;
}

.cositas {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 1fr;
  gap: 0 10px;
  grid-template-areas: ". .";
}

.TodoList {
  width: 100%;
  display: flex;
  flex-direction: column;
  max-height: 60vh;

  overflow-y: auto;
}

.TodoItem {
  width: 100%;
  border-radius: 50px;
  background: #1b1b1b;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  min-height: 4rem;
}

.completado {
  display: flex;
  align-items: center;
  margin: 0 20px;
}

.cuerpo-contenido {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 20px;
}

.cuerpo-contenido i {
  cursor: pointer;
}

