/* From uiverse.io by @Alaner-xs */
/* Hide the default checkbox */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.container {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 20px;
  user-select: none;
}

/* Create a custom checkbox */
.checkmark {
  position: relative;
  top: 0;
  left: 0;
  height: 1.3em;
  width: 1.3em;
  background-color: #ccc;
  border-radius: 15%;
  box-shadow: 0px 0px 17px #c7bbb8;
}

/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: #bf21f3;
  box-shadow: 0px 0px 17px #bf21f3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
  left: 0.45em;
  top: 0.25em;
  width: 0.25em;
  height: 0.5em;
  border: solid rgb(233, 222, 222);
  border-width: 0 0.15em 0.15em 0;
  transform: rotate(45deg);
}

/*  */

.icon-btn {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-radius: 50%;
  background: #bf21f3;
  cursor: pointer;
  border: none;
}

.icon-btn i {
  font-size: 1.5rem;
  text-shadow: 0 0 3.5px #fff, 0 0 5px rgb(179, 0, 255),
    0 0 10.5px rgb(179, 0, 255), 0 0 21px rgb(244, 223, 253);
}

.center {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.TodoList h2 {
  margin: 40px 0;
}

/* Loader */

/* From uiverse.io by @xXJollyHAKERXx */
.spinner {
  background-image: linear-gradient(rgb(186, 66, 255) 35%, rgb(0, 225, 255));
  width: 75px;
  height: 75px;
  animation: spinning82341 1.7s linear infinite;
  text-align: center;
  border-radius: 50px;
  filter: blur(1px);
  box-shadow: 0px -5px 20px 0px rgb(186, 66, 255),
    0px 5px 20px 0px rgb(0, 225, 255);
  margin: 20px;
}

.spinner1 {
  background-color: rgb(36, 36, 36);
  width: 100%;
  height: 100%;
  border-radius: 50px;
  filter: blur(10px);
}

@keyframes spinning82341 {
  to {
    transform: rotate(360deg);
  }
}

/* Loader */

.FormModal {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.FormCard {
  width: 80%;
  max-width: 600px;
  background: #202020;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-height: 80vh;
}

.card-head {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.card-head i {
  font-size: 2.5rem;
  cursor: pointer;
}

.card-body {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  overflow-y: auto;
}

.card-body::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.card-body::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}

.form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "."
    ".";
}

/* Input form */

/* From uiverse.io by @Cornerstone-04 */
.input-form {
  width: 100%;
  height: 30px;
  border: 2px solid transparent;
  outline: none;
  border-bottom: 2px solid #3f3f3f;
  caret-color: #3f3f3f;
  background-color: #212121;
  padding: 5px;
  transition: 0.5s linear;
  font-family: monospace;
  letter-spacing: 1px;
}

.input-form:focus {
  border: 2px solid #bf21f3;
  caret-color: #bf21f3;
  color: #bf21f3;
  box-shadow: 4px 4px 10px #070707;
}

.input-form:focus::placeholder {
  color: #bf21f3;
}

