/* Buttomn form */

/* From uiverse.io by @EmmaxPlay */
.btn {
  padding: 0.9em 1.6em;
  border: none;
  outline: none;
  color: #fff;
  font-family: inherit;
  font-weight: 500;
  font-size: 17px;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 12px;
  margin-top: 20px;
}

.btn::after {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #202020;
  left: 0;
  top: 0;
  border-radius: 10px;
}
/* glow */
.btn::before {
  content: "";
  background: linear-gradient(
    45deg,
    #002bff,
    #bf21f3,
    #002bff,
    #ff0000,
    #002bff,
    #bf21f3,
    #002bff
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  filter: blur(8px);
  animation: glowing 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
  opacity: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

/* hover */
.btn:hover::before {
  opacity: 1;
}

.btn:active:after {
  background: #20202034;
}

.btn:active {
  color: #000;
  font-weight: bold;
}

/* Buttomn form */

.HideCompleted {
  padding: 0px 20px;
  margin-bottom: 10px;
}

.HideCompleted p,
i {
  color: rgba(255, 255, 255, 0.286);
  cursor: pointer;
}

.AppFooter {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 20px 0;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: ". .";
}

.AppFooter p {
  margin: 0 20px;
  color: rgba(255, 255, 255, 0.146);
}

.icons {
  display: flex;
  justify-self: flex-end;
  margin: 0 20px;
}

.icons i {
  margin: 5px;
  color: rgba(255, 255, 255, 0.146);
}

.TodoList::-webkit-scrollbar {
  width: 7px;
}

.TodoList::-webkit-scrollbar-thumb {
  background: #bf21f36d;
  border-radius: 4px;
}

.TodoList::-webkit-scrollbar-thumb:hover {
  background: #bf21f39c;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

.TodoList::-webkit-scrollbar-thumb:active {
  background-color: #bf21f3;
}

.TodoItem-buttons {
  display: flex;
  gap: 20px;
  align-items: center;
}

.tip-button {
  color: #ffc800;

  transition: 0.3s;

  filter: drop-shadow(0px 0px 1px #ffc800);
}

.tip-button:hover {
  filter: drop-shadow(0px 0px 10px #ffc800) !important;
}

.tip-button-seen {
  color: #5c5c5c;

  transition: 0.3s;

  filter: drop-shadow(0px 0px 0px #5c5c5c);
}

.tip-button-seen:hover {
  filter: drop-shadow(0px 0px 10px #ffffff) !important;
}

.loaderaddtodo {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: calc(0.9em + 17px + 20px);
}

.loaderaddtodo p {
  opacity: 0.3;
}

.loader-add {
  position: relative;
  display: flex;
  gap: 0.3em;
}

.loader-add::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2em;
  filter: blur(45px);
  background-color: #e299ff;
  background-image: radial-gradient(
      at 52% 57%,
      hsla(11, 83%, 72%, 1) 0px,
      transparent 50%
    ),
    radial-gradient(at 37% 57%, hsla(175, 78%, 66%, 1) 0px, transparent 50%);
}

.loader__circle-add {
  --size__loader: 0.6em;
  width: var(--size__loader);
  height: var(--size__loader);
  border-radius: 50%;
  animation: loader__circle__jumping 2s infinite;
  background-color: #b499ff;
}

.loader__circle-add:nth-child(2n) {
  animation-delay: 300ms;
  background-color: #e499ff;
}

.loader__circle-add:nth-child(3n) {
  animation-delay: 600ms;
}

@keyframes loader__circle__jumping {
  0%,
  100% {
    transform: translateY(0px);
  }

  25% {
    transform: translateY(-15px) scale(0.5);
  }

  50% {
    transform: translateY(0px);
  }

  75% {
    transform: translateY(5px) scale(0.9);
  }
}

@media (max-width: 768px) {
  .contenedor {
    width: calc(100% - 20px);
    min-height: calc(100vh - 20px);
    margin-top: 0;
    padding: 10px;
  }

  .cuerpo-primer-contenedor {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1.5rem 0px;
    grid-template-areas:
      "."
      ".";
  }

  .cuerpo {
    padding: 0px;
  }

  .cositas {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  } 

  .input {
    font-weight: 500;
    font-size: 5vw;
    width: 100%;
  }

  .TodoList {
    max-height: 65vh;
  }

  .center {
    width: 100%;
    height: 60vh;
  }

  .form {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0px 00px;
    grid-template-areas:
      "."
      ".";
  }

  .AppFooter {
    position: relative;
  }

  .TodoList::-webkit-scrollbar {
    display: none;
  }
}
